import React, { useEffect, useMemo, useRef, useState } from "react";
import styled from "styled-components";

import { deleteFaq, 
    deleteHistory, 
    deleteHistoryCategory, 
    deleteProduct,
    historyCategoryApi,
    updateHistory,
    updateHistoryCategory, 
 
} from "../../api/Apis";

import { ListType } from "../../interface/Interface";

import ReactQuill from "react-quill";

import "react-quill/dist/quill.snow.css";

const HistoryCategory = ( data:ListType ) => {

    const [ year_start, setYearStart ] = useState(data.year_start || '2022');
    const [ year_end, setYearEnd ] = useState(data.year_end || '-');

    const [ content, setContent ] = useState<string>(data.content);

    const [ historyCategory, setHistoryCategory ] = useState([]);
    const [ historyCategoryId, setHistoryCategoryId ] = useState(data.ref_id);

    const deleteButtonHandle = () => {  

        if(window.confirm("정말로 삭제하시겠습니까?")) {

            deleteHistoryCategory(data.id).then(res => {
                if(res.success) {
                    window.location.href = "/main";
                }
            });
        }

    };

    const saveButtonHandle = () => {  

        let updateData:any = [];

        updateData['id']            = data.id;
        updateData['startYear']     = year_start;
        updateData['endYear']       = year_end;
        updateData['content']       = content;
        
        updateHistoryCategory(updateData).then(res => {
            if(res.success) {
                window.location.href = "/main";
            }
        });

    };

    const contentHandle = (e:any) => {
        setContent(e.target.value);
    }

    const yearOnChangeHandle = (e:any) => { setYearStart(e.target.value); }

    const yearEndOnChangeHandle = (e:any) => { setYearEnd(e.target.value); }

    useEffect(() => { 

        if(!data.id) {
            alert("히스토리 ID를 확인할 수 없습니다. [History Detail Missing data.id]");
            window.location.href = "/main";
        }

        historyCategoryApi().then((res) => {
            setHistoryCategory(res.data);
        })

    }, []);

    const renderYearOptions = () => {

        let yearInit = 1900;
        let yearMax = 2100;

        const result = [];

        result.push(<option key="empty" value={0} >-</option>);

        for (let i = yearInit; i < yearMax; i++) {

            result.push(<option key={i} value={i} >{i}년</option>);
            
        }

        return result;
    }

    return (
        <Container>

            {/* Top Options & Info Section */}
            <Options>

                {/* Delete Button */}
                <DeleteButton
                    onClick={deleteButtonHandle}
                >
                    삭제
                </DeleteButton>

                <SaveButton
                    onClick={saveButtonHandle}
                >
                    저장
                </SaveButton>

                <h4> </h4>
            </Options>

            <DataSection>

                {/* Data Id */}
                <Row height={50}>
                    <Col width={20}>고유번호</Col>
                    <Col width={80}>{ data.id }</Col>
                </Row>

                <Row height={50}>
                    <Col width={20}>시작연도</Col>
                    <Col width={80}>
                        <select 
                            onChange={(e) => yearOnChangeHandle(e)}
                            value={ year_start }
                        >
                            {renderYearOptions()}
                        </select>
                    </Col>
                </Row> 

                <Row height={50}>
                    <Col width={20}>마감연도</Col>
                    <Col width={80}>
                        <select 
                            onChange={(e) => yearEndOnChangeHandle(e)}
                            value={ year_end }
                        >
                            {renderYearOptions()}
                        </select>
                    </Col>
                </Row> 

                <Row height={50}>
                    <Col width={20}>상세내용</Col>
                    <Col width={80}>
                    <input type="text" onChange={(e) => contentHandle(e)} value={content || ""}/>  
                    </Col>
                </Row> 

            </DataSection>

        </Container>
    )
}

export default HistoryCategory;

