import React, { useEffect, useMemo, useRef, useState } from "react";
import styled from "styled-components";

import { 
    deleteProduct, deleteQna, updateQna, 
} from "../../api/Apis";

import { ListType } from "../../interface/Interface";


const Qna = ( data:ListType ) => {

    const deleteButtonHandle = () => {  

        if(window.confirm("정말로 삭제하시겠습니까?")) {

            deleteQna(data.id).then(res => {
                if(res.success) {
                    window.location.href = "/main";
                }
            });
        }

    };

    // is Video? play
    useEffect(() => { 

        if(!data.id) {
            alert("문의사항 ID를 확인할 수 없습니다. [QNA Detail Missing data.id]");
            window.location.href = "/main";
        }

    }, []);

    const updateButtonHandle = () => {
        // 상태 변경
        if(window.confirm("답변을 완료하신 상태로 변경하시겠어요?")) {
            // 
            updateQna(data.id).then(res => {
                if(res.success) {
                    window.location.href = "/main";
                }
            });
        }
    }

    return (
        <Container>

            {/* Top Options & Info Section */}
            <Options>

                {/* Delete Button */}
                <DeleteButton
                    onClick={deleteButtonHandle}
                >
                    삭제
                </DeleteButton>

                <SaveButton
                    onClick={updateButtonHandle}
                >
                    답변완료
                </SaveButton>

                <h4>  </h4>
            </Options>

            <DataSection>

                {/* Data Id */}
                <Row height={40}>
                    <Col width={20}>고유번호</Col>
                    <Col width={80}>{ data.id }</Col>
                </Row>

                <Row height={40}>
                    <Col width={20}>상태</Col>
                    <Col width={80}>
                        { data.status == '1' ? "답변완료" : "답변대기중"}
                    </Col>
                </Row>

                <Row height={40}>
                    <Col width={20}> 문의유형 </Col>
                    <Col width={80}>{ data.category }</Col>
                </Row>

                <Row height={40}>
                    <Col width={20}>문의사</Col>
                    <Col width={80}>{ data.company }</Col>
                </Row> 

                <Row height={40}>
                    <Col width={20}>문의자</Col>
                    <Col width={80}>{ data.name }</Col>
                </Row> 

                <Row height={40}>
                    <Col width={20}>회신이메일</Col>
                    <Col width={80}>{ data.email } </Col>
                </Row> 

                <Row height={40}>
                    <Col width={20}>회신유선</Col>
                    <Col width={80}>{ data.phone }</Col>
                </Row>

                <Row height={40}>
                    <Col width={20}>작성일</Col>
                    <Col width={80}>{ data.created_at }</Col>
                </Row>
                
                <Row height={320}>
                    <Col width={20}>문의내용</Col>
                    <Col width={80}>
                        <textarea readOnly defaultValue={data.content} />
                    </Col>
                </Row>

            </DataSection>

        </Container>
    )
}

export default Qna;

